"use client";

import React, { useState } from "react";
import Head from "next/head";
import InitialUI from "./InitialUI";
import ChatBoxUI from "./ChatBoxUI";

export default function HomePage() {
  const [message, setMessage] = useState("");
  const [chatStarted, setChatStarted] = useState(false);
  const [chatLog, setChatLog] = useState<
    { sender: "user" | "ai"; type: "text"; content: string }[]
  >([]);

  const handleSendMessage = () => {
    if (message.trim() === "") return;
    if (!chatStarted) setChatStarted(true);
    setChatLog((prev) => [...prev, { sender: "user", type: "text", content: message }]);
    setMessage("");
    // Simulate AI response
    setTimeout(() => {
      setChatLog((prev) => [
        ...prev,
        { sender: "ai", type: "text", content: `Response to "${message}"` },
      ]);
    }, 1000);
  };

  return (
    <>
      <Head>
        <title>BlackboxAI</title>
        <meta name="viewport" content="width=device-width, initial-scale=1" />
        <link
          href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css"
          rel="stylesheet"
        />
        <script src="https://cdn.tailwindcss.com"></script>
      </Head>
      {!chatStarted ? (
        <InitialUI message={message} setMessage={setMessage} onSendMessage={handleSendMessage} />
      ) : (
        <ChatBoxUI
          message={message}
          setMessage={setMessage}
          chatLog={chatLog}
          onSendMessage={handleSendMessage}
        />
      )}
    </>
  );
}
